import { put, delay } from "redux-saga/effects";
import axios from "../../axios-orders";
import * as actions from "../actions/index";

export function* purchaseStartSaga(action) {
  yield put(actions.purchaseBurger());
  const res = yield axios.post(
    `/orders.json?auth=${action.token}`,
    action.orderData
  );
  try {
    yield put(actions.purchaseSuccess(res.data.name, action.orderData));
  } catch (error) {
    yield put(actions.purchaseFail(error));
  }
  yield delay(3000);
  yield put(actions.purchaseRedirect());
}

export function* fetchOrdersSaga(action) {
  yield put(actions.fetchOrdersStart());
  const res = yield axios.get(
    `/orders.json?auth=${action.token}&orderBy="userOrderId"&equalTo="${action.userOrderId}"`
  );
  try {
    const fetchedOrders = [];
    for (let key in res.data) {
      fetchedOrders.push({ ...res.data[key], id: key });
    }
    yield put(actions.fetchOrdersSuccess(fetchedOrders));
  } catch (error) {
    yield put(actions.fetchOrdersFail(error));
  }
}
