import { put, delay } from "redux-saga/effects";
import axios from "axios";

import * as actions from "../actions/index";

export function* checkAuthTimeoutSaga(action) {
  yield delay(action.expiredTime);
  yield put(actions.logout());
}

export function* storeContactDataSaga(action) {
  yield axios.post(
    `https://react-my-burger-df27e-default-rtdb.firebaseio.com/users.json?auth=${action.token}`,
    action.contactData
  );

  yield put(actions.storeSuccess(action.contactData));
}

export function* fetchContactDataSaga(action) {
  yield put(actions.fetchContactStart());
  const res = yield axios.get(
    `https://react-my-burger-df27e-default-rtdb.firebaseio.com/users.json?auth=${action.token}&orderBy="email"&equalTo="${action.email}"`
  );

  yield put(actions.storeSuccess(Object.values(res.data)[0]));
}

export function* authSaga(action) {
  yield put(actions.authStart());
  const authData = {
    email: action.email,
    password: action.password,
    returnSecureToken: true,
  };
  let url =
    "https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyCGKuK-wRQmRIcpQsQ13FyWXeW-6M5dNmI";
  if (!action.isSignup) {
    url =
      "https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyCGKuK-wRQmRIcpQsQ13FyWXeW-6M5dNmI ";
  }
  const res = yield axios.post(url, authData);
  try {
    const expirationData = yield new Date(
      new Date().getTime() + res.data.expiresIn * 1000
    );
    yield put(
      actions.authSuccess(res.data.idToken, res.data.localId, action.email)
    );
    yield put(actions.checkAuthTimeout(res.data.expiresIn * 1000));
    yield localStorage.setItem("token", res.data.idToken);
    yield localStorage.setItem("expiredDate", expirationData);
    yield localStorage.setItem("userOrderId", res.data.localId);
    yield localStorage.setItem("email", action.email);
    if (action.isSignup) {
      yield put(actions.storeContactData(action.contactData, res.data.idToken));
    }
  } catch (error) {
    yield put(actions.authFail(error.response.data.error));
  }
}

export function* authCheckStateSaga(action) {
  const token = yield localStorage.getItem("token");
  if (!token) {
    yield put(actions.logout());
  } else {
    const expirationData = yield new Date(localStorage.getItem("expiredDate"));
    if (expirationData < new Date()) {
      yield put(actions.logout());
    } else {
      const email = yield localStorage.getItem("email");
      const userOrderId = yield localStorage.getItem("userOrderId");
      yield put(actions.authSuccess(token, userOrderId, email));
      yield put(
        actions.checkAuthTimeout(
          expirationData.getTime() - new Date().getTime()
        )
      );
    }
  }
}
