import * as actionTypes from "./actionTypes";

export const authStart = () => {
  return { type: actionTypes.AUTH_START };
};

export const authSuccess = (token, id, email) => {
  return {
    type: actionTypes.AUTH_SUCCESS,
    token: token,
    userOrderId: id,
    email: email,
  };
};

export const authFail = (error) => {
  return {
    type: actionTypes.AUTH_FAIL,
    error: error,
  };
};

export const logout = () => {
  return {
    type: actionTypes.LOGOUT,
  };
};

export const checkAuthTimeout = (expiredTime) => {
  return {
    type: actionTypes.AUTH_CHECK_TIMEOUT,
    expiredTime: expiredTime,
  };
};

export const storeSuccess = (contactData) => {
  return {
    type: actionTypes.STORE_SUCCESS,
    contactData: contactData,
  };
};

export const storeContactData = (contactData, token) => {
  return {
    type: actionTypes.INIT_STORE_DATA,
    contactData: contactData,
    token: token,
  };
};
export const fetchContactStart = () => {
  return {
    type: actionTypes.FETCH_CONTACT_START,
  };
};
export const fetchContactData = (token, email) => {
  return {
    type: actionTypes.INIT_FETCH_DATA,
    token: token,
    email: email,
  };
};
export const authSwitch = () => {
  return {
    type: actionTypes.AUTH_SWITCH,
  };
};

export const auth = (email, password, isSignup, contactData) => {
  return {
    type: actionTypes.AUTH_INIT,
    email: email,
    password: password,
    isSignup: isSignup,
    contactData: contactData,
  };
};

export const authCheckState = () => {
  return {
    type: actionTypes.AUTH_CHECK_STATE,
  };
};
