import { takeEvery, all } from "redux-saga/effects";
import {
  checkAuthTimeoutSaga,
  storeContactDataSaga,
  fetchContactDataSaga,
  authSaga,
  authCheckStateSaga,
} from "./auth";
import { initIngredientsSaga } from "./burgerBuilder";
import { purchaseStartSaga, fetchOrdersSaga } from "./order";
import * as actionTypes from "../actions/actionTypes";

export function* watchAuth() {
  yield all([
    takeEvery(actionTypes.AUTH_CHECK_TIMEOUT, checkAuthTimeoutSaga),
    takeEvery(actionTypes.INIT_STORE_DATA, storeContactDataSaga),
    takeEvery(actionTypes.INIT_FETCH_DATA, fetchContactDataSaga),
    takeEvery(actionTypes.AUTH_INIT, authSaga),
    takeEvery(actionTypes.AUTH_CHECK_STATE, authCheckStateSaga),
  ]);
}

export function* watchBurgerBuilder() {
  yield all([
    takeEvery(actionTypes.INIT_INGREDIENTS_SAGA, initIngredientsSaga),
  ]);
}
export function* watchOrder() {
  yield all([
    takeEvery(actionTypes.PURCHASE_START, purchaseStartSaga),
    takeEvery(actionTypes.FETCH_ORDERS_INIT, fetchOrdersSaga),
  ]);
}
